
import React, { useState } from 'react';
import { Form, Button } from 'react-bootstrap';

interface LeftFormProps {
  onAddStock: (stock: string, alert: number) => void;
}

const LeftForm: React.FC<LeftFormProps> = ({ onAddStock }) => {
  const [stock, setStock] = useState('');
  const [alertPrice, setAlertPrice] = useState(0);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (stock && alertPrice) {
      onAddStock(stock, alertPrice);
      setStock('');
      setAlertPrice(0);
    }
  };

  return (
    <Form onSubmit={handleSubmit} className="p-4 border rounded bg-light">
      <Form.Group className="mb-3" controlId="stockSelect">
        <Form.Label>Select Stock:</Form.Label>
        <Form.Control
          as="select"
          value={stock}
          onChange={(e) => setStock(e.target.value)}
        >
          <option value="">-- Choose Stock --</option>
          <option value="AAPL">Apple (AAPL)</option>
          <option value="GOOGL">Google (GOOGL)</option>
          <option value="AMZN">Amazon (AMZN)</option>
        </Form.Control>
      </Form.Group>

      <Form.Group className="mb-3" controlId="priceAlert">
        <Form.Label>Price Alert:</Form.Label>
        <Form.Control
          type="number"
          value={alertPrice}
          onChange={(e) => setAlertPrice(Number(e.target.value))}
        />
      </Form.Group>

      <Button variant="primary" type="submit" className="w-100">
        Add
      </Button>
    </Form>
  );
};

export default LeftForm;
